import { useDispatch, useSelector } from 'react-redux'

export const useChartProps = (setProps, loadCharts) => {

    const chartProps = useSelector(state => state.bitcoinModule.chartProps)
    const dispatch = useDispatch()

    const onUpdateProps = (key, val) => {
        dispatch(setProps({ [key]: val }))
        dispatch(loadCharts())
    }

    const register = (key, val) => {
        return ({
            onClick: () => onUpdateProps(key, val),
            className: `flex center btn-statistics ${chartProps[key] === val ? 'active' : ''}`,
        })
    }

    return [register]
}