import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'

export const useForm = (initState, saveFields, removeFields) => {

    const [fields, setFields] = useState(initState)
    const navigate = useNavigate()
    const dispatch = useDispatch()

    const handleChange = ({ target }) => {
        const field = target.name
        let value = target.value
        const initials = target.name === 'name'
            ? target.value.trim().split(' ').map(word => word.charAt(0)).join('').toUpperCase()
            : fields.initials || fields.name.trim().split(' ').map(word => word.charAt[0]).join('').toUpperCase()
        switch (target.type) {
            case 'number':
            case 'range':
                value = +value
                break;
            case 'checkbox':
                value = target.checked
                break
            default:
                break;
        }
        setFields(prevState => ({ ...prevState, [field]: value, initials }))
    }

    const register = (field, type = '') => ({
        onChange: handleChange,
        type,
        name: field,
        value: fields[field],
        className: 'input block ' + field,
        placeholder: 'Contact ' + field
    })

    const save = async ev => {
        ev.preventDefault()
        try {
            const savedFields = await dispatch(saveFields({ ...fields }))
            if (fields._id) navigate('/contact/edit/' + fields._id)
            navigate('/contact/' + savedFields._id)
        } catch (err) {
            console.log(`err`, err)
        }
    }

    const remove = () => {
        try {
            dispatch(removeFields(fields._id))
            navigate('/contact')
        } catch (err) {
            console.log(`err`, err)
        }
    }

    return [fields, register, setFields, save, remove]
}